/* 5. Backup related */

label.backupnow_includecloud {
	/*cursor:pointer;*/
}

/* Below is adapted from: http://www.hongkiat.com/blog/css3-checkbox-radio/ */
.modal-body label {
	display: inline-block;
	cursor: pointer;
	position: relative;
	font-size: 13px;
	margin-top: 20px;
	margin-bottom: 0;
}

label.backupnow_includedb::before {
	content: "";
	display: inline-block;
	width: 16px;
	height: 16px;
	margin-right: 10px;
	position: absolute;
	left: 0;
	bottom: 1px;
	background-color: #EAEAEA;
	border-radius: 4px;
}

.updraft_existing_backups .rescanning {
	font-style: italic;
	padding-bottom: 10px;
}

label[for=backupnow_label]::before {
	display: none;
}

label[for=backupnow_label] {
	padding-left: 0;
}

#updraftcentral_notice_container .updraftcentral_notice .updraftplus_downloader .dlfileprogress {
	width: 0;
	background: #F6A828;
	height: 5px;
}

.updraftplus_downloader .raw {
	margin: 8px 0 10px 0;
}

.updraftplus_downloader .file {
	display: block;
	height: auto;
	padding: 5px;
	background: #ECECEC;
	border: solid 1px #CCC;
	margin: 8px 0 0 0;
