	font-size: 100%;
	font-weight: bold;
	border: 0px;
	border-radius: 4px;
	padding: 2px;
	margin: 0 6px;
}

span.updraftplus-morefiles-row-delete:hover, span.updraft_retain_rules_delete:hover {
	color: #FFF;
	background-color: #F00;
	cursor: pointer;
}

.updraft_site_settings_output tr#remote_storage_tabs td {
	border-bottom: 1px solid #D1D1D1;
	padding-bottom: 0;
}

